@import 'color';

#profile{
    width: 100%;
    height: 55vh;
    background-color: rgb(0, 0, 0);
    color:rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    padding: 5%;
    justify-content: space-between;
    align-items: center;

    button{
        background-color: transparent;
        border-color: #fff;
        border: solid;
        border-width: thin;
        color: white;
        padding: 10px 30px;
        margin-top: 30px;
    }
    
}

.product{
    &__name{
        color:black;

    }
}

#profileOrders{
    width: 100%;
    background-color: rgb(255, 255, 255);
    padding: 5%;
}

.order{
    width: 100%;
    background-color: transparent;
    border: solid;
    border-width: thin;
    border-radius: 5px;
    border-color: #ccc;
    color:rgb(36, 36, 36);
    margin-bottom:25px;
    display: flex;
    padding: 5%;

    &__info{
        margin-right: 20%;
    }
}

#chartContainer {
    padding: 5%;
}